.searchForm {
  display: flex;
  gap: 10px;
  padding: 20px;
  border-bottom: 1px solid rgb(170, 166, 166);
  box-shadow: 1px 1px 4px 1px rgb(170, 166, 166, 0.6);
  border-radius: 2px;
  background-color: rgb(218, 216, 216, 0.2);
}
.searchInput {
  outline: none;
  border: 1px solid rgb(170, 166, 166);
  border-radius: 4px;
}
.searchBtn {
  border: 1px solid rgb(170, 166, 166);
  padding: 10px;
  border-radius: 4px;
}
.searchBtn:hover {
  scale: calc(1.02);
}
.searchBtn:active {
  background-color: rgb(218, 216, 216, 0.2);
}
